.container {
  min-height: 100vh;
  padding: 0 0.5rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #e8f0f4;
}

.title {
  margin: 0;
  line-height: 1.15;
  font-size: 4rem;
  text-align: center;
}

.imagen1 {
  padding-top: 2rem;
  display: flex;
  justify-content: center;
  align-items: center;
}

.description {
  line-height: 1.5;
  font-size: 1.5rem;
  text-align: center;
}

.projects {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  margin-top: 3rem;
  border: 2px solid black;
  padding: 1rem;
  margin: 1rem;
}

.projectCard {
  background-color: #f5f5f5;
  border-radius: 8px;
  padding: 1.5rem;
  margin: 1rem;
  text-align: center;
  width: 300px;
}

.projectCard h2 {
  margin-bottom: 1rem;
}

.projectCard p {
  margin-bottom: 1.5rem;
}

.projectCard a {
  color: #0070f3;
}

.section {
  align-items: center;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 1rem;
  margin-top: 1rem;
  padding-left: 10rem;
}

.section h2 {
  font-size: 2rem;
  font-weight: bold;
  margin-bottom: 1rem;
}

.section ul {
  list-style-type: disc;
  padding-left: 2rem;
}

.section li {
  margin-bottom: 0.5rem;
}
